                    <path id="_5282541_fb_social_media_facebook_facebook_logo_social_network_icon" data-name="5282541_fb_social media_facebook_facebook logo_social network_icon" d="M24.393,0a3.4,3.4,0,0,1,3.395,3.395v21a3.4,3.4,0,0,1-3.395,3.395H18.614V17.318h3.615l.688-4.484h-4.3V9.924A2.242,2.242,0,0,1,21.142,7.5H23.1V3.684a23.858,23.858,0,0,0-3.472-.3c-3.543,0-5.859,2.148-5.859,6.035v3.418H9.828v4.484h3.939v10.47H3.395A3.4,3.4,0,0,1,0,24.393v-21A3.4,3.4,0,0,1,3.395,0h21Z" fill="#fff" fillRule="evenodd" />
