import axios from 'axios'
import { useEffect, useState, useContext } from 'react'
import LangContext from '../../Context/lang';


const Info = ({ Step, setStep, StepTitel, setFormdata, dar }) => {

  const { Lang, Global, dartaksitsim, Account, RecapitulatifSimTaksit, dari3marsim, Panier, infoperssonnelle } = useContext(LangContext);


  const [Civilite, setCivilite] = useState('M')
  const [Nom, setNom] = useState('')
  const [Prenom, setPrenom] = useState('')
  const [PrenomPere, setPrenomPere] = useState('')
  const [NomMere, setNomMere] = useState('')
  const [PrenomMere, setPrenomMere] = useState('')
  const date = localStorage.getItem(`${dar}Year`) + '-' + localStorage.getItem(`${dar}Month`) + '-' + localStorage.getItem(`${dar}day`);
  const [Datedenaissance, setDatedenaissance] = useState(date)
  const [Wilaya, setWilaya] = useState('')
  const [Commune, setCommune] = useState('')
  const [CommuneList, setCommuneList] = useState([])
  const [Actedenaissance, setActedenaissance] = useState('')
  const [Typeactnaissnce, setTypeactnaissnce] = useState('')
  const [Situationfamiliale, setSituationfamiliale] = useState('')
  const [NiveauDinstructions, setNiveauDinstructions] = useState('')
  const [Children, setChildren] = useState(0)
  const [Personnes, setPersonnes] = useState(0)
  // const [MaxQuantity, setMaxQuantity] = useState(2)

  const [FormValidate, setFormValidate] = useState(true)
  const [Showerr, setShowerr] = useState(false)
  const [FormChecked, setFormChecked] = useState(false)

  useEffect(() => {


    Validation();

  }, [Civilite, Nom, Prenom, PrenomPere, NomMere, PrenomMere, Wilaya, Commune, Children, Datedenaissance, Actedenaissance, Typeactnaissnce, Situationfamiliale, NiveauDinstructions, Personnes])



  useEffect(() => {

    if (Showerr) {
      const matches = document.querySelectorAll(".alert-form");

      for (let index = 0; index < matches.length; index++) {

        if (matches[index].textContent.trim() !== '') {

          let position = matches[index].getBoundingClientRect();


          window.scrollTo(position.left, position.top + window.scrollY - 120);
          return
        }
        // if (matches[index].textContent.trim() !== '') {

        //     matches[index].scrollIntoView({behavior: "smooth"});
        //     return
        // }              
      }
    }


  }, [Showerr])


  function GoNext() {
    setShowerr(true)

    console.log(FormValidate);

    if (!FormValidate) {

      const matches = document.querySelectorAll(".alert-form");

      for (let index = 0; index < matches.length; index++) {

        if (matches[index].textContent.trim() !== '') {

          let position = matches[index].getBoundingClientRect();


          window.scrollTo(position.left, position.top + window.scrollY - 120);
          return
        }
        // if (matches[index].textContent.trim() !== '') {

        //     matches[index].scrollIntoView({behavior: "smooth"});
        //     return
        // }              
      }

      return
    }

    setFormdata((prev) => [
      {
        ...prev[0], ...{
          "r_civilite": Civilite,
          "nom": Nom,
          "prenom": Prenom,
          "prenompere": PrenomPere,
          "nommere": NomMere,
          "prenommere": PrenomMere,
          "datenaissance": DateFormat(Datedenaissance),
          "wnaissance": Wilaya,
          "communen": Commune,
          "numActNaissance": Actedenaissance,
          "typeActNaissance": Typeactnaissnce,
          "stfamiliale": Situationfamiliale,
          "niveau": NiveauDinstructions,
          "nbenfants": Children.toString(),
          "nbenfantsplus": Personnes.toString(),
        }
      }
    ])

    setStep(2)
    window.scrollTo(0, 0);


    console.log('test', FormValidate);
  }

  function onlyLetters(str) {
    return /^[a-zA-Z\s]+$/.test(str);
  }
  function dateIsValid(date) {
    return date instanceof Date && !isNaN(date);
  }
  function onlyNumbers(str) {
    return /^[0-9]+$/.test(str);
  }

  function DateFormat(date) {

    var d = new Date(date);
    var datestring = d.getFullYear() + "/" + d.getMonth().toString().padStart(2, '0') + "/" + d.getDate().toString().padStart(2, '0');

    return datestring;

  }

  function Validation() {

    setFormValidate(true)

    if (Civilite !== "M" && Civilite !== "Mme" && Civilite !== "Mle") {
      setFormValidate(false)
    }
    if (!onlyLetters(Nom) || Nom.trim().length < 2) {
      setFormValidate(false)
    }
    if (!onlyLetters(Prenom) || Prenom.trim().length < 2) {
      setFormValidate(false)
    }
    if (!onlyLetters(PrenomPere) || PrenomPere.trim().length < 2) {
      setFormValidate(false)
    }
    if (!onlyLetters(NomMere) || NomMere.trim().length < 2) {
      setFormValidate(false)
    }
    if (!onlyLetters(PrenomMere) || PrenomMere.trim().length < 2) {
      setFormValidate(false)
    }
    if (!dateIsValid(new Date(Datedenaissance))) {
      setFormValidate(false)
    }
    if (Wilaya === "") {
      setFormValidate(false)
    }
    if (Commune === "") {
      setFormValidate(false)
    }
    if (!onlyNumbers(Actedenaissance) || Actedenaissance.trim().length > 5) {
      setFormValidate(false)
    }
    if (Typeactnaissnce !== "biss" && Typeactnaissnce !== "ter" && Typeactnaissnce !== "autre") {
      setFormValidate(false)
    }
    if (Situationfamiliale !== "c" && Situationfamiliale !== "m" && Situationfamiliale !== "d" && Situationfamiliale !== "v") {
      setFormValidate(false)
    }
    if (NiveauDinstructions !== "1" && NiveauDinstructions !== "2" && NiveauDinstructions !== "3" && NiveauDinstructions !== "4") {
      setFormValidate(false)
    }
    if (!onlyNumbers(Children)) {
      setFormValidate(false)
    }
    if (!onlyNumbers(Personnes)) {
      setFormValidate(false)
    }


  }


  console.log(Civilite, Nom, Prenom, PrenomPere, Wilaya, Commune, Children, Datedenaissance, Actedenaissance, Typeactnaissnce, Situationfamiliale, NiveauDinstructions);

  useEffect(() => {

    const GetCommune = async () => {
      try {
        const { data: response } = await axios.get(`https://www.diardzair.com.dz/ws/region/dairas?valeur=${Wilaya}`);
        console.log(response);
        setCommuneList(response['codepostals'])
      } catch (error) {
        console.error(error.message);
      }
    }

    if (Wilaya !== '') {
      GetCommune();
    }



  }, [Wilaya])



  return (
    <div className={`form-info row form-1 ${Step !== 1 ? 'd-none' : ''}`}>
      <div className='text-center info-step-titel mb-3'>{StepTitel[Step - 1]}</div>
      {/* <form className="row g-3 m-auto justify-content-center"> */}
      <div className="col-md-12">
        <div className='mb-2'>{(infoperssonnelle['civilité'][Lang])} </div>
        <div className="form-check form-check-inline">
          <input className="form-check-input" type="radio" name="inlineRadioOptions" id="inlineRadio1" checked={Civilite == 'M'} onChange={(e) => setCivilite(e.target.value)} defaultChecked={true} defaultValue="M" />
          <label className="form-check-label" htmlFor="inlineRadio1">{(infoperssonnelle['civ1'][Lang])}</label>
        </div>
        <div className="form-check form-check-inline">
          <input className="form-check-input" type="radio" name="inlineRadioOptions" id="inlineRadio2" checked={Civilite == 'Mme'} onChange={(e) => setCivilite(e.target.value)} defaultValue="Mme" />
          <label className="form-check-label" htmlFor="inlineRadio2">{(infoperssonnelle['civ2'][Lang])}</label>
        </div>
        <div className="form-check form-check-inline">
          <input className="form-check-input" type="radio" name="inlineRadioOptions" id="inlineRadio3" checked={Civilite == 'Mle'} onChange={(e) => setCivilite(e.target.value)} defaultValue="Mle" />
          <label className="form-check-label" htmlFor="inlineRadio3">{(infoperssonnelle['civ3'][Lang])}</label>
        </div>
      </div>

      <div className="col-md-6 mb-5 mt-5">
        <label htmlFor="inputEmail4" className="form-label">{(infoperssonnelle['Nom'][Lang])}</label>
        <input type="text" placeholder={(infoperssonnelle['Nom'][Lang])} onChange={(e) => setNom(e.target.value.replace(/[^a-zA-Z\s]+/g, ''))} value={Nom} className="form-control" id="inputEmail4" />
        <div className='alert-form'>{Showerr && (Nom.trim().length < 2 ? (infoperssonnelle['msgerr1'][Lang]) : '')}</div>
        <div className='alert-form'>{Showerr && (!onlyLetters(Nom) ? (infoperssonnelle['msgerr2'][Lang]) : '')}</div>


      </div>
      <div className="col-md-6 mb-5 mt-md-5">
        <label htmlFor="inputEmail4" className="form-label">{(infoperssonnelle['Prénom'][Lang])}</label>
        <input type="text" placeholder={(infoperssonnelle['Prénom'][Lang])} onChange={(e) => setPrenom(e.target.value.replace(/[^a-zA-Z\s]+/g, ''))} value={Prenom} className="form-control" id="inputEmail4" />
        <div className='alert-form'>{Showerr && (Prenom.trim().length < 2 ? (infoperssonnelle['msgerr3'][Lang]) : '')}</div>
        <div className='alert-form'>{Showerr && (!onlyLetters(Prenom) ? (infoperssonnelle['msgerr4'][Lang]) : '')}</div>
      </div>
      <div className="col-md-6 mb-5">
        <label htmlFor="inputEmail4" className="form-label">{(infoperssonnelle['Prénompère'][Lang])}</label>
        <input type="text" placeholder={(infoperssonnelle['Prénompère'][Lang])} onChange={(e) => setPrenomPere(e.target.value.replace(/[^a-zA-Z\s]+/g, ''))} value={PrenomPere} className="form-control" id="inputEmail4" />
        <div className='alert-form'>{Showerr && (PrenomPere.trim().length < 2 ? (infoperssonnelle['msgerr5'][Lang]) : '')}</div>
        <div className='alert-form'>{Showerr && (!onlyLetters(PrenomPere) ? (infoperssonnelle['msgerr6'][Lang]) : '')}</div>
      </div>
      <div className="col-md-6 mb-5">
        <label htmlFor="inputEmail4" className="form-label">{(infoperssonnelle['Nommère'][Lang])}</label>
        <input type="text" placeholder={(infoperssonnelle['Nommère'][Lang])} onChange={(e) => setNomMere(e.target.value.replace(/[^a-zA-Z\s]+/g, ''))} value={NomMere} className="form-control" id="inputEmail4" />
        <div className='alert-form'>{Showerr && (NomMere.trim().length < 2 ? (infoperssonnelle['msgerr7'][Lang]) : '')}</div>
        <div className='alert-form'>{Showerr && (!onlyLetters(NomMere) ? (infoperssonnelle['msgerr8'][Lang]) : '')}</div>
      </div>
      <div className="col-md-6 mb-5">
        <label htmlFor="inputEmail4" className="form-label">{(infoperssonnelle['Prenommère'][Lang])}</label>
        <input type="text" placeholder={(infoperssonnelle['Prenommère'][Lang])} onChange={(e) => setPrenomMere(e.target.value.replace(/[^a-zA-Z\s]+/g, ''))} value={PrenomMere} className="form-control" id="inputEmail4" />
        <div className='alert-form'>{Showerr && (PrenomMere.trim().length < 2 ? (infoperssonnelle['msgerr9'][Lang]) : '')}</div>
        <div className='alert-form'>{Showerr && (!onlyLetters(PrenomMere) ? (infoperssonnelle['msgerr10'][Lang]) : '')}</div>
      </div>
      <div className="col-md-6 mb-5">
        <label htmlFor="inputEmail4" className="form-label">{(infoperssonnelle['Date_naissance'][Lang])}</label>
        <input type="date" onChange={(e) => setDatedenaissance(e.target.value)} defaultValue={Datedenaissance} className="form-control" id="inputEmail4" />
        <div className='alert-form'>{Showerr && (Datedenaissance == "" ? (infoperssonnelle['msgerr11'][Lang]) : '')}</div>
      </div>
      <div className="col-md-6 mb-5">
        <label htmlFor="inputEmail4" className="form-label">{(infoperssonnelle['Wilaya_naissance'][Lang])}</label>
        <select class="form-select" aria-label="Default select example" onChange={(e) => setWilaya(e.target.value)}>
          <option disabled selected="selected">{(Global['wilaya'][Lang])}</option>
          <option value="ADRAR">(01) ADRAR</option>
          <option value="CHLEF">(02) CHLEF</option>
          <option value="LAGHOUAT">(03) LAGHOUAT</option>
          <option value="OUM EL BOUAGHI">(04) OUM-EL-BOUAGHI</option>
          <option value="BATNA">(05) BATNA</option>
          <option value="BEJAIA">(06) BEJAIA</option>
          <option value="BISKRA">(07) BISKRA</option>
          <option value="BECHAR">(08) BECHAR</option>
          <option value="BLIDA">(09) BLIDA</option>
          <option value="BOUIRA">(10) BOUIRA</option>
          <option value="TAMANRASSET">(11) TAMANRASSET</option>
          <option value="TEBESSA">(12) TEBESSA</option>
          <option value="TLEMCEN">(13) TLEMCEN</option>
          <option value="TIARET">(14) TIARET</option>
          <option value="TIZI OUZOU">(15) TIZI-OUZOU</option>
          <option value="ALGER">(16) ALGER</option>
          <option value="DJELFA">(17) DJELFA</option>
          <option value="JIJEL">(18) JIJEL</option>
          <option value="SETIF">(19) SETIF</option>
          <option value="SAIDA">(20) SAIDA</option>
          <option value="SKIKDA">(21) SKIKDA</option>
          <option value="SIDI BEL ABBES">(22) SIDI-BEL-ABBES</option>
          <option value="ANNABA">(23) ANNABA</option>
          <option value="GUELMA">(24) GUELMA</option>
          <option value="CONSTANTINE">(25) CONSTANTINE</option>
          <option value="MEDEA">(26) MEDEA</option>
          <option value="MOSTAGANEM">(27) MOSTAGANEM</option>
          <option value="M SILA">(28) M-SILA</option>
          <option value="MASCARA">(29) MASCARA</option>
          <option value="OUARGLA">(30) OUARGLA</option>
          <option value="ORAN">(31) ORAN</option>
          <option value="EL BAYADH">(32) EL-BAYADH</option>
          <option value="ILLIZI">(33) ILLIZI</option>
          <option value="BORDJ BOU ARRERIDJ">(34) BORDJ-BOU-ARRERIDJ</option>
          <option value="BOUMERDES">(35) BOUMERDES</option>
          <option value="EL TARF">(36) EL-TARF</option>
          <option value="TINDOUF">(37) TINDOUF</option>
          <option value="TISSEMSILT">(38) TISSEMSILT</option>
          <option value="EL OUED">(39) EL-OUED</option>
          <option value="KHENCHELA">(40) KHENCHELA</option>
          <option value="SOUK AHRAS">(41) SOUK-AHRAS</option>
          <option value="TIPAZA">(42) TIPAZA</option>
          <option value="MILA">(43) MILA</option>
          <option value="AIN DEFLA">(44) AIN-DEFLA</option>
          <option value="NAAMA">(45) NAAMA</option>
          <option value="AIN TEMOUCHENT">(46) AIN-TEMOUCHENT</option>
          <option value="GHARDAIA">(47) GHARDAIA</option>
          <option value="RELIZANE">(48) RELIZANE</option>
          <option value="Timimoun">(49) Timimoun</option>
          <option value="Bordj Badji Mokhtar">(50) Bordj Badji Mokhtar</option>
          <option value="Ouled Djellal">(51) Ouled Djellal</option>
          <option value="Béni Abbès">(52) Béni Abbès</option>
          <option value="In Salah">(53) In Salah</option>
          <option value="In Guezzam">(54) In Guezzam</option>
          <option value="Touggourt">(55) Touggourt</option>
          <option value="Djanet">(56) Djanet</option>
          <option value="El M'Ghair">(57) El M'Ghair</option>
          <option value="El Meniaa">(58) El Meniaa</option>
        </select>
        <div className='alert-form'>{Showerr && (Wilaya == "" ? (infoperssonnelle['msgerr12'][Lang]) : '')}</div>
      </div>
      <div className="col-md-6 mb-5">
        <label htmlFor="inputPassword4" className="form-label">{(infoperssonnelle['Commune_naissance'][Lang])}</label>
        <select class="form-select" aria-label="Default select example" onChange={(e) => setCommune(e.target.value)}>

          {
            CommuneList.length == 0 && <option disabled selected="selected">{(infoperssonnelle['Commune_naissance'][Lang])}</option>
          }

          {
            CommuneList.length > 0 && <option disabled selected="selected">{(infoperssonnelle['Commune_naissance'][Lang])}</option>
          }

          {CommuneList.length > 0 && CommuneList.map((item, i) => (

            <option value={item.dairas}>{item.dairas}</option>

          ))
          }
        </select>
        <div className='alert-form'>{Showerr && (Commune == "" ? (infoperssonnelle['msgerr13'][Lang]) : '')}</div>
      </div>
      {/* <label htmlFor="inputPassword4" className="form-label">Numéro d'acte de naissance</label> */}
      <div className="col-md-6 mb-5">
        <label htmlFor="inputEmail4" className="form-label">{(infoperssonnelle['Num_act_naissance'][Lang])}</label>
        <input type="text" maxlength="5" placeholder={(infoperssonnelle['Num_act_naissance'][Lang])} onChange={(e) => setActedenaissance(e.target.value.replace(/[^0-9]+/g, ''))} value={Actedenaissance} className="form-control me-2" id="inputPassword4" />
        <div className='alert-form'>{Showerr && (Actedenaissance == "" || Actedenaissance.trim().length !== 5  ? (infoperssonnelle['msgerr14'][Lang]) : '')}</div>
      </div>
      <div className="col-md-6 mb-5">
        {/* <input type="text" onChange={(e) => setActedenaissance(e.target.value.replace(/[^0-9]+/g , ''))} value={Actedenaissance} className="form-control me-2" id="inputPassword4" /> */}
        <label htmlFor="inputEmail4" className="form-label">{(infoperssonnelle['type_acte'][Lang])}</label>
        <select class="form-select" aria-label="Default select example" onChange={(e) => setTypeactnaissnce(e.target.value)}>
          <option disabled selected="selected">{(infoperssonnelle['type_acte'][Lang])}</option>
          <option value="biss">BISS </option>
          <option value="ter">TER</option>
          <option value="autre">Autre</option>
        </select>
        <div className='alert-form'>{Showerr && (Typeactnaissnce == "" ? (infoperssonnelle['msgerr15'][Lang]) : '')}</div>
      </div>
      <div className="col-md-6 mb-5">
        <label htmlFor="inputPassword4" className="form-label" >{(infoperssonnelle['situation_familiale'][Lang])}</label>
        <select class="form-select" aria-label="Default select example" onChange={(e) => setSituationfamiliale(e.target.value)}>
          <option disabled selected="selected">{(infoperssonnelle['situation_familiale'][Lang])}</option>
          <option value="c">Célibataire</option>
          <option value="m">Marié </option>
          <option value="d">Divorcé</option>
          <option value="v">veuf</option>
        </select>
        <div className='alert-form'>{Showerr && (Situationfamiliale == "" ? (infoperssonnelle['msgerr16'][Lang]) : '')}</div>
      </div>
      <div className="col-md-6 mb-5">
        <label htmlFor="inputPassword4" className="form-label" >{(infoperssonnelle['Niveau'][Lang])}</label>
        <select class="form-select" aria-label="Default select example" onChange={(e) => setNiveauDinstructions(e.target.value)}>
          <option disabled selected="selected">{(infoperssonnelle['Niveau'][Lang])}</option>
          <option value={1}>Moins que le niveau secondaire</option>
          <option value={2}>Niveau secondaire</option>
          <option value={3}>Bachelier</option>
          <option value={4}>Universitaire</option>
        </select>
        <div className='alert-form'>{Showerr && (NiveauDinstructions == "" ? (infoperssonnelle['msgerr17'][Lang]) : '')}</div>
      </div>
      <div className="col-md-12 mb-2 mt-3">
        <label htmlFor="inputPassword4" className="form-label">{(infoperssonnelle['nbrEnfants'][Lang])}</label>
        <div className='product-quantity'>{(infoperssonnelle['1'][Lang])} :
          <div className='quantity-btn'>
            <button className="qtyminus" onClick={() => Children !== 0 ? setChildren(Children - 1) : ''} aria-hidden="true">−</button>
            <input className='qtyinput' onKeyPress="event.preventDefault();" type="number" name="qty" id="qty" min={1} step={1} value={Children} />
            <button className="qtyplus" onClick={() => Children < 10 ? setChildren(Children + 1) : ''} aria-hidden="true">+</button>
          </div>
        </div>
        {/* <input type="number" max={2} min={0} className="form-control" id="inputPassword4" /> */}
      </div>
      <div className="col-md-12 mb-5 mt-3">
        <label htmlFor="inputPassword4" className="form-label">{(infoperssonnelle['nbrpersonnes'][Lang])}</label>
        <div className='product-quantity'>{(infoperssonnelle['2'][Lang])} :
          <div className='quantity-btn'>
            <button className="qtyminus" onClick={() => Personnes !== 0 ? setPersonnes(Personnes - 1) : ''} aria-hidden="true">−</button>
            <input className='qtyinput' onKeyPress="event.preventDefault();" type="number" name="qty" id="qty" min={1} step={1} value={Personnes} />
            <button className="qtyplus" onClick={() => Personnes < 10 ? setPersonnes(Personnes + 1) : ''} aria-hidden="true">+</button>
          </div>
        </div>
        {/* <input type="number" max={2} min={0} className="form-control" id="inputPassword4" /> */}
      </div>
      {/* <div className="col-md-12 mb-3">
    <label htmlFor="inputPassword4" className="form-label">Nombre d'autres personnes à charge
</label>
    <input type="number" max={2} min={0} className="form-control" id="inputPassword4" />
  </div> */}



      {/* </form> */}

      <div className='row mt-5'>
        <div className="col text-end">
          <button type="submit" onClick={() => GoNext()} className="next-info-btn">{(dari3marsim['Suivant'][Lang])}
          <svg className='btn-next' xmlns="http://www.w3.org/2000/svg" width="44.906" height="33.07" viewBox="0 0 44.906 33.07">
                        <g id="show-more-icon-5" transform="translate(639.487 234.637) rotate(180)">
                          <path id="Tracé_254" data-name="Tracé 254" d="M37.627,18.609H3.229C1.029,18.609.021,18,0,16.666s1.008-2.005,3.167-2.005H37.761c-.5-.555-.782-.895-1.1-1.193q-4.982-4.674-9.975-9.327c-.236-.216-.473-.422-.689-.658A1.885,1.885,0,0,1,25.883.655,1.835,1.835,0,0,1,28.7.665q7.728,7.2,15.425,14.438a1.925,1.925,0,0,1,.041,3.064q-7.142,7.126-14.335,14.191a1.83,1.83,0,0,1-2.818.093,1.871,1.871,0,0,1,.134-2.931q4.674-4.643,9.358-9.276c.411-.411.895-.74,1.347-1.111C37.781,18.959,37.709,18.784,37.627,18.609Z" transform="translate(639.487 234.637) rotate(180)" fill="#fffcfc" />
                        </g>
                      </svg>
          </button>

          {/* {
    FormValidate ? 
    <button type="submit" onClick={() => GoNext() } className="next-info-btn">Suivant</button>
    :
    <button className="next-info-btn-disable">Suivant</button>
   } */}
        </div>
      </div>
    </div>
  )
}

export default Info